import React from 'react'

import styles from './Payouts.module.scss'
import topImg from '../../img/payouts/payouts-top-img.png'
import TopBlockWithImage from '../../components/TopBlockWithImage/TopBlockWithImage'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'

const Payouts = (props) => {

    const paragraphsList = [
        {
            title: '  ',
            text: '    : , , , 2.'
        },
        {
            title: '  ',
            text: '       .'
        },
        {
            title: '   ',
            text: '     (e-mail, sms, whatsapp, telegram, viber).'
        },
        {
            title: '  ',
            text: '           .'
        },
        {
            title: '  QR- ',
            text: '     QR-   .'
        },
        {
            title: 'AFT ',
            text: '             .'
        },
        {
            title: '  ',
            text: '             .'
        },
        {
            title: ' ',
            text: '            -.'
        },
    ]

    return (
        <div className={styles.Payouts}>
            <TopBlockWithImage
                topBlockImg={topImg}
                topBlockItemText1={'     1 '}
                topBlockItemText2={'-          ,  ,     .'}
            
            />

            <SkewedBlock>
                <AdaptiveParagraphs>

                    {
                        paragraphsList.map(item => <ParagraphWithTitle {...item} key={item.title}/>)
                    }
                   
                </AdaptiveParagraphs>

            </SkewedBlock>
        </div>
    )
}

export default Payouts