import React, { Fragment } from 'react'
import ImageWithShadow from '../../ImageWithShadow/ImageWithShadow'
import Button from '../../UI/Button/Button'
import paymentTerminal from '../../../img/legal-entity-main/payment-terminal.png'
import integrationImg from '../../../img/legal-entity-main/integrationImg.png'
import widgetImg from '../../../img/legal-entity-main/widgetImg.png'


import styles from './InfoBlock.module.scss'

const InfoBlock = ({ image, buttonText, show = 'technology', history }) => {

    const technology = (
        <Fragment>
            <h1>    </h1>
            <p>
                      -.       .
                    </p>
            <p> :  , Apple Pay, Google Pay, Samsung Pay,  ,     .</p>

        </Fragment>
    )

    const integration = (
        <Fragment>
            <h1>  </h1>
            <p>
                  ,  ,     .
             </p>
            <p>
                      API.  SDK-        iOS, Android.   CMS.    .
             </p>

        </Fragment>
    )

    const widget = (
        <Fragment>
            <h1> </h1>
            <p>
                              .
            </p>
            <p>
                       .        .   PCI DSS
            </p>
        </Fragment>
    )

    const navMenuContent = { technology, integration, widget }

    const navItemImages = {
        technology: paymentTerminal,
        integration: integrationImg,
        widget: widgetImg
    }

    const buttonsText = {
        technology: '  ',
        integration: '  ',
        widget: '  '
    }
    const buttonLinks = {
        technology: '/developers',
        integration: '/developers/connection',
        widget: '/developers/payment-widget'
    }

    return (
        <div className={styles.InfoBlock}>
            <div className={styles.infoBlockText}>

                {navMenuContent[show]}

                <div className={styles.btnBlock}>
                    <Button
                        text={buttonsText[show]}
                        type='green'
                        className={styles.btn}
                        clickHander={() => history.push(buttonLinks[show])}
                    />
                </div>

            </div>

            <ImageWithShadow image={navItemImages[show]} className={styles.image} />

        </div>
    )
}

export default InfoBlock