import React from 'react'

import styles from './SelfEmployed.module.scss'
import topBlockImg from '../../img/self-employed/topBlockimg.png'
import paymentSteps from '../../img/self-employed/payment-steps.svg'
import startListImg from '../../img/self-employed/start-list.png'
import TopBlockWithImage from '../../components/TopBlockWithImage/TopBlockWithImage'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import AdaptiveParagraphs from '../../components/AdaptiveParagraphs/AdaptiveParagraphs'
import ParagraphWithTitle from '../../components/AdaptiveParagraphs/ParagraphWithTitle/ParagraphWithTitle'
import TextWithTick from '../../components/UI/TextWithTick/TextWithTick'
import SelfEmployedTariffs from './SelfEmployedTariffs/SelfEmployedTariffs'

const SelfEmployed = (props) => {

    const topBlockItemText1 = '    '
    const topBlockItemText2 = '        ,        .       API   .'

    const youLikeList = [
        {
            title: ' ,  ',
            text: '         API.   ,        .            .'
        },
        {
            title: '  API',
            text: '   ,   ,     .  API          .'
        },
        {
            title: ' ',
            text: '     .           115-.'
        },
        {
            title: '   ',
            text: 'VEPAY  .     ,  ,     ,  .'
        },
        {
            title: '    ',
            text: '       ,       .    API    .'
        },
    ]

    const startList = [
        {
            type: 'violet',
            text: '  '
        },
        {
            type: 'violet',
            text: '     '
        },
        {
            type: 'violet',
            text: ' ,  '
        },
        {
            type: 'violet',
            text: ' '
        },
        {
            type: 'violet',
            text: ',     API'
        },
    ]

    return (
        <div className={styles.SelfEmployed}>

            <TopBlockWithImage
                topBlockImg={topBlockImg}
                topBlockItemText1={topBlockItemText1}
                topBlockItemText2={topBlockItemText2}
            />

            <SkewedBlock>
                <h1> VEPAY</h1>
                <div>
                    <AdaptiveParagraphs>
                        <p>    ,   : ,     .</p>
                        <p><strong>   -</strong>,   </p>
                        <p><strong> -</strong>,     </p>
                        <p> ,         (    ).</p>
                        <p> <strong> </strong>,   , , , -, ,     </p>

                    </AdaptiveParagraphs>

                </div>
            </SkewedBlock>

            <div className={styles.paymentStepsBlock}>
                <h1>   </h1>
                <div className={styles.paymentSteps}>
                    <img className={styles.paymentStepsImg} src={paymentSteps} alt="paymentSteps" />

                    <p className={styles.step1}>
                        <span></span>
                              .
                    </p>
                    <p className={styles.step2}>
                        <span></span>
                            . ,   .
                    </p>
                    <p className={styles.step3}>
                        <span></span>
                            ,     .       (  ,  ).
                        </p>
                    <p className={styles.step4}>
                        <span></span>
                            .           .
                        </p>
                </div>
            </div>


            <SkewedBlock>

                <h1> </h1>
                <div>
                    <AdaptiveParagraphs>
                        {
                            youLikeList.map(item => <ParagraphWithTitle {...item} key={item.title} />)
                        }
                    </AdaptiveParagraphs>
                </div>
            </SkewedBlock>

            <div className={styles.startListBlock}>
                <h1>  </h1>
                <div className={styles.startListBlockItem}>
                    <div className={styles.startList}>
                        {
                            startList.map(item => <TextWithTick {...item} key={item.text}/>)
                        }
                    </div>
                    <img className={styles.startListImg} src={startListImg} alt="startList"/>
                </div>


                <SelfEmployedTariffs/>
            </div>



        </div>
    )
}

export default SelfEmployed