import React, { useState } from 'react'

import styles from './Tariffs.module.scss'
import topImg from '../../img/tariffs/tariffs-top-img.png'
import internetShopImg from '../../img/tariffs/internet-shops.png'
import aviaImg from '../../img/tariffs/avia.png'
import housingImg from '../../img/tariffs/housing-services.png'
import AdaptiveFlexBlock from '../../components/Blocks/AdaptiveFlexBlock/AdaptiveFlexBlock'
import AdaptiveColumn from '../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import SkewedBlock from '../../components/SkewedBlock/SkewedBlock'
import CardWithButton from '../../components/Cards/CardWithButton/CardWithButton'

const Tariffs = (props) => {

    const [intShopValue, setIntShopValue] = useState(' 1 P')
    const [aviaValue, setAviaValue] = useState(' 1 P')
    const [housingValue, setHousingValue] = useState(' 1 P')

    const intShopPercent = {
        " 1 P": '2,7 %',
        " 1   5 ": '2,6 %',
        " 5   15 ": '2,5 %',
        " 15   30 ": '2,3 %',
        " 30   ": ' '
    }

    const aviaPercent = {
        " 1 P": '1,7 %',
        " 1   5 ": '1,6 %',
        " 5   15 ": '1,4 %',
        " 15   30 ": '1,2 %',
        " 30   ": ' '
    }

    const housingPercent = {
        " 1 P": '1,5 %',
        " 1   5 ": '1,3 %',
        " 5   15 ": '1,1 %',
        " 15   30 ": '0,9 %',
        " 30   ": ' '
    }

    return (
        <div className={styles.Tariffs}>
            
            <AdaptiveFlexBlock className={styles.topBlock}>
                <AdaptiveColumn header=''>
                    <p className={styles.topText}>
                     VEPAY        ,    .
                    </p>

                </AdaptiveColumn>
                <AdaptiveColumn>
                    <img className={styles.topImg} src={topImg} alt="topImg"/>
                </AdaptiveColumn>
            </AdaptiveFlexBlock>

            <SkewedBlock>
                <div className={styles.tariffsBlock}>
                    <CardWithButton  header='-' image={internetShopImg}
                    >
                        <p className={styles.cardText1}>  </p>
                        <select 
                            value={intShopValue} 
                            name="internet-shops" 
                            id="internet-shops"
                            onChange={(e) => setIntShopValue(e.target.value)}
                        >
                            <option value=" 1 P"> 1 P</option>
                            <option value=" 1   5 "> 1   5 </option>
                            <option value=" 5   15 "> 5   15 </option>
                            <option value=" 15   30 "> 15   30 </option>
                            <option value=" 30   "> 30   </option>
                        </select>
                        <p>
                            <small>  VEPAY</small>
                        </p>
                        <h1>{intShopPercent[intShopValue]}</h1>
                        
                    </CardWithButton>
                    <CardWithButton header=''  image={aviaImg}>
                        <p className={styles.cardText1}>  </p>
                        <select 
                            value={aviaValue} 
                            name="avia" 
                            id="avia"
                            onChange={(e) => setAviaValue(e.target.value)}
                        >
                            <option value=" 1 P"> 1 P</option>
                            <option value=" 1   5 "> 1   5 </option>
                            <option value=" 5   15 "> 5   15 </option>
                            <option value=" 15   30 "> 15   30 </option>
                            <option value=" 30   "> 30   </option>
                        </select>
                        <p>
                            <small>  VEPAY</small>
                        </p>
                        <h1>{aviaPercent[aviaValue]}</h1>
                        
                    </CardWithButton>
                    <CardWithButton header=''  image={housingImg}>
                        <p className={styles.cardText1}>  </p>
                        <select 
                            value={housingValue} 
                            name="housing" 
                            id="housing"
                            onChange={(e) => setHousingValue(e.target.value)}
                        >
                            <option value=" 1 P"> 1 P</option>
                            <option value=" 1   5 "> 1   5 </option>
                            <option value=" 5   15 "> 5   15 </option>
                            <option value=" 15   30 "> 15   30 </option>
                            <option value=" 30   "> 30   </option>
                        </select>
                        <p>
                            <small>  VEPAY</small>
                        </p>
                        <h1>{housingPercent[housingValue]}</h1>
                        
                    </CardWithButton>
                </div>
            </SkewedBlock>

            <div className={styles.bottomTextBlock}>
                <p className={styles.bottomText}>
                     ,      .


                </p>
            </div>

        </div>
    )
}

export default Tariffs